@font-face {
  font-family: "PT Mono";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("PT Mono-regular"), url("../fonts/pt-mono-v8-latin_cyrillic-ext-regular.woff2") format("woff2"), url("../fonts/pt-mono-v8-latin_cyrillic-ext-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Roboto Condensed-regular"), url("../fonts/roboto-condensed-v19-latin_cyrillic-ext-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-v19-latin_cyrillic-ext-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("Roboto Condensed-bold"), url("../fonts/roboto-condensed-v19-latin_cyrillic-ext-700.woff2") format("woff2"), url("../fonts/roboto-condensed-v19-latin_cyrillic-ext-700.woff") format("woff"); }

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

.page {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow-y: scroll;
  scroll-behavior: smooth;
  height: 100%; }

.page-body {
  min-height: 100%;
  background-color: #ffffff;
  color: #000000;
  font-family: "PT Mono", "Courier New", monospace;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  line-height: 21px; }

.page-body h1 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase; }
